.App {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f8f8f8;
}

.progress {
    position: absolute;
    top: 20px;
    left: 20px;
    font-size: 18px;
    color: #333;
}

.character-display {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: "Arial", sans-serif;
    font-size: 96px;
    font-weight: bold;
}

.japanese-character {
    font-size: 100px;
    color: #000;
}

.pronunciation {
    margin-top: 20px;
    font-size: 32px;
    color: #555;
}
